import React from "react";
import { Link } from "react-router-dom";
import { pluralize } from "../../utils/helpers"
import { useStoreContext } from "../../utils/GlobalState";
import { ADD_TO_CART, UPDATE_CART_QUANTITY } from "../../utils/actions";
import { idbPromise } from "../../utils/helpers";
import TicketList from "../TicketList";

function Ticket(ticket) {
  const [state, dispatch] = useStoreContext();

  const {
    image,
    name,
    _id,
    price,
    quantity
  } = ticket;

  const { cart } = state

  const addToCart = () => {
    const ticketInCart = cart.find((cartTicket) => cartTicket._id === _id)
    if (ticketInCart) {
      dispatch({
        type: UPDATE_CART_QUANTITY,
        _id: _id,
        purchaseQuantity: parseInt(ticketInCart.purchaseQuantity) + 1
      });
      idbPromise('cart', 'put', {
        ...ticketInCart,
        purchaseQuantity: parseInt(ticketInCart.purchaseQuantity) + 1
      });
    } else {
      dispatch({
        type: ADD_TO_CART,
        ticket: { ...ticket, purchaseQuantity: 1 }
      });
      idbPromise('cart', 'put', { ...ticket, purchaseQuantity: 1 });
    }
  }

  return (
    <div className="card px-1 py-1">
      <Link to={`/tickets/${_id}`}>
        <img
          alt={name}
           src={`client/public/images/Angels.jpg`}
          src={`/images/Angels.jpg`}
        />
        <p>{name}</p>
      </Link>
     
     <TicketList />
      
      <div>
        <div>{quantity} {pluralize("ticket", quantity)} in stock</div>
        <span>${price}</span>
      </div>
      <button onClick={addToCart}>Add to cart</button>
    </div>
  );
}

export default Ticket;
